.App {
  font-family: sans-serif;
  text-align: center;
}
.result-block {
  font-size: larger;
  height: 100px;
  width: 100px;
  text-align: center;
  margin: 20px auto;
  background-color: rgb(184, 168, 168);
  align-items: center;
  line-height: 100px;
  font-weight: 900;
  border-radius: 50px;
}
.action-button {
  height: 40px;
  padding: 10px;
  display: inline;
  margin: 5px;
  border: 3px solid rgb(184, 168, 168);
  cursor: pointer;
}
.app-heading {
  color: rgb(86, 83, 83);
}
hr {
  width: 80%;
  height: 2px;
  background-color: rgb(86, 83, 83);
}
